import { Box, Button, Grid, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux';
import Chart from 'react-apexcharts';
import Visibility from '@mui/icons-material/Visibility';
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';
import moment from 'moment';
import { useNavigate } from 'react-router-dom';



export const ChartShow = () => {
    const commonReducer = useSelector((state) => state.commonReducer);
    const navigate = useNavigate();


    const [options, setOptions] = useState({
        chart: {
            id: 'apexchart-example'
        },
        xaxis: {
            categories: []
        }
    });
    const [series, setSeries] = useState([
        {
            name: 'series-1',
            data: []
        }
    ]);

    useEffect(() => {
        const price = [];
        const date = [];
        if (commonReducer?.chartData?.length > 0) {
            commonReducer.chartData.forEach(item => {
                price.push(item.price);
                date.push(moment(item?.date).format("MMM DD"));
            });
            setOptions({
                ...options,
                xaxis: {
                    ...options.xaxis,
                    categories: date
                }
            });
            setSeries([
                {
                    ...series[0],
                    data: price
                }
            ]);
        }
    }, [commonReducer?.chartData]);

    const viewClickHandler = () => {
        console.log('commonReducer?.chartData', commonReducer?.chartData)
        navigate('/chartDetails')
    }

    const backClickHandler = () => {
        navigate('/dashboard')
    }

    return <Box>
        <Grid container justifyContent={'center'} alignItems={'center'} minHeight={'100vh'}>
            <Grid item xs={10} bgcolor={'#f9f8ff'} p={2}>
                <Grid container item justifyContent={'flex-end'} p={'10px 0px'} alignItems={'center'}>
                    <Button onClick={viewClickHandler} variant='contained' style={{ backgroundColor: "#002266" }} startIcon={<Visibility />}>View</Button>
                    <DeleteOutlineIcon style={{ color: "#fff", backgroundColor: "#e62965", padding: "6.5px 10px", borderRadius: "5px", margin: "0px 10px", cursor: "pointer" }} />
                </Grid>
                <Typography sx={{ color: "#002266", fontWeight: "bold" }}>Crude Palm Oil</Typography>
                <Typography color={'#b1afaf'} fontSize={'12px'} p={'5px 0px'}>Crude Palm Oil | USDMT | Indonesia</Typography>
                <Chart options={options} series={series} type="line" width={'100%'} height={420} />
                <Typography sx={{ color: "#002266", fontSize: "10px", textAlign: "center", paddingBottom: "10px", fontWeight: "bold" }}>-Crude Palm Oil(Indonesia, Spot FD, USD/MT)</Typography>
            </Grid>
            <Grid item xs='6'>
                <Button variant='contained' fullWidth sx={{ backgroundColor: "#002265" }} onClick={backClickHandler}>Back Changes</Button>
            </Grid>
        </Grid>
    </Box>
}
