import { Box, Button, Grid, Typography } from '@mui/material'
import React, { useEffect, useState } from 'react'
import AddIcon from '@mui/icons-material/Add';
import RemoveCircleIcon from '@mui/icons-material/RemoveCircle';
import RemoveIcon from '@mui/icons-material/Remove';
import { CustomTextField } from '../Components/CustomTextField';
import { afterValidate, getErrorMsz } from '../Components/Validator';
import { updateState } from '../redux/commonSlice';
import { useDispatch, useSelector } from 'react-redux';
import Chart from 'react-apexcharts';
import { useNavigate } from 'react-router-dom';



export const Dashboard = () => {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const [submitFlag, setSubmitFlag] = useState(false)

    const data = {
        price: "",
        date: ""
    }

    const [pagedata, setPagedata] = useState([data])
    const addClickHandler = () => {
        var localArray = [...pagedata]
        localArray.push(data);
        setPagedata(localArray)
    }
    const removeClickHandler = (index) => {
        var localArray = pagedata.filter((_, currentIndex) => {
            return index !== currentIndex
        })
        setPagedata(localArray)
    }
    const updateClickHandler = (key, value, index) => {
        var localArray = pagedata.map((item, currentIndex) => {
            return (index === currentIndex) ? { ...item, [key]: value } : item
        })
        setPagedata(localArray)
    }

    const afterValidateCallBack = () => {
        dispatch(updateState({ chartData: pagedata }))
        navigate('/chart')
    }

    const addChangesClickHandler = () => {
        afterValidate(afterValidateCallBack)
        setSubmitFlag(true)
    }

    return <Box p={2}>
        <Grid container justifyContent={'center'}>
            <Grid item xs='12' border={'1px solid #ccc'}>
                <Grid container item alignItems={'center'} justifyContent={'space-between'} p={'20px 30px'} >
                    <Typography color={'#141657'} fontWeight={'bold'}>Manage Product Price Trends</Typography>
                    <Button variant='contained' style={{ backgroundColor: "#141657" }}
                        onClick={addChangesClickHandler}
                    >Add Changes</Button>
                </Grid>
            </Grid>
            <Grid item xs='12' lg='9'>
                {pagedata.map((item, index) => (
                    <Grid container alignItems='center' key={index} p='20px 0px' spacing={1}>
                        <Grid item xs={10} sm={5} md={5} lg={5}>
                            <CustomTextField
                                type='number'
                                value={item.price}
                                placeholder='New Price..'
                                onChange={(e) => updateClickHandler("price", e.target.value, index)}
                                required
                                error={submitFlag && getErrorMsz('price', item?.price) != ""}
                                errorMsz={getErrorMsz('price', item?.price)}
                            />
                        </Grid>
                        <Grid item xs={10} sm={5} md={5} lg={5}>
                            <CustomTextField
                                type='date'
                                value={item.date}
                                onChange={(e) => updateClickHandler("date", e.target.value, index)}
                                required
                                error={submitFlag && getErrorMsz('date', item?.date) != ""}
                                errorMsz={getErrorMsz('date', item?.date)}
                            />
                        </Grid>
                        <Grid item xs={2} display={'flex'} justifyContent={'center'}>
                            <Box>
                                {index === 0 ? (
                                    <AddIcon className='add-btn' onClick={addClickHandler} />
                                ) : (
                                    <RemoveIcon className='remove-btn' onClick={() => removeClickHandler(index)} />
                                )}
                            </Box>
                        </Grid>
                    </Grid>
                ))}
            </Grid>
        </Grid>
    </Box>
}
