import { Box, Button, Grid, Typography } from '@mui/material';
import moment from 'moment';
import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux';
import Chart from 'react-apexcharts';
import HomeIcon from '@mui/icons-material/Home';
import NotificationsNoneIcon from '@mui/icons-material/NotificationsNone';
import AddIcon from '@mui/icons-material/Add';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import NoteAddIcon from '@mui/icons-material/NoteAdd';
import FilterAltIcon from '@mui/icons-material/FilterAlt';
import { useNavigate } from 'react-router-dom';



export const ChartDetails = () => {
    const commonReducer = useSelector((state) => state.commonReducer);
    const [totalPrice, setTotalPrice] = useState("")
    const navigate = useNavigate();

    const [options, setOptions] = useState({
        chart: {
            id: 'apexchart-example'
        },
        xaxis: {
            categories: []
        }
    });
    const [series, setSeries] = useState([
        {
            name: 'series-1',
            data: []
        }
    ]);
    useEffect(() => {
        const price = [];
        const date = [];
        if (commonReducer?.chartData?.length > 0) {
            commonReducer.chartData.forEach(item => {
                price.push(item.price);
                date.push(moment(item?.date).format("MMM DD"));
            });
            setOptions({
                ...options,
                xaxis: {
                    ...options.xaxis,
                    categories: date
                }
            });
            setSeries([
                {
                    ...series[0],
                    data: price
                }
            ]);
        }

    }, [commonReducer?.chartData]);


    useEffect(() => {
        const sum = commonReducer?.chartData.reduce((acc, curr) => acc + parseInt(curr.price), 0);
        setTotalPrice(sum)
    }, [commonReducer?.chartData])


    const backClickHandler = () => {
        navigate('/chart')
    }

    return <Box>
        <Grid container>
            <Grid item xs='12' className='back-grid-effect'>
            </Grid>
            <Grid item xs='12' position={'relative'} bottom={'190px'}>
                <Grid container item spacing={3}>
                    <Grid item xs='12' lg='7.9'>
                        <Box bgcolor={'#f9f8ff'} borderRadius={'8px'} ml={3} p={'20px'} border={'1px solid #ccc'}>
                            <Grid container item justifyContent={'space-between'} p={'10px 0px'}>
                                <Grid item xs='12' sm='6' md='5' lg='4'>
                                    <Grid container item justifyContent={'space-around'}>
                                        <HomeIcon className='home-icon' />
                                        <Typography className='years-show'>1Y</Typography>
                                        <Typography className='years-show'>2Y</Typography>
                                        <Typography className='years-show'>5Y</Typography>
                                        <Typography className='years-show'>10Y</Typography>
                                        <Typography className='years-show'>ALL</Typography>
                                    </Grid>
                                </Grid>
                                <Grid item xs='12' sm='6' md='5' lg='4'>
                                    <Grid container item justifyContent={'space-around'}>
                                        <Typography className='years-show'>Update</Typography>
                                        <NotificationsNoneIcon className='icons' />
                                        <AddIcon className='icons' />
                                        <AccessTimeIcon className='icons' />
                                        <NoteAddIcon className='icons' />
                                        <FilterAltIcon className='icons' />
                                    </Grid>
                                </Grid>
                            </Grid>
                            <Typography sx={{ color: "#002266", fontWeight: "bold" }}>Crude Palm Oil</Typography>
                            <Typography color={'#b1afaf'} fontSize={'12px'} p={'5px 0px'}>Crude Palm Oil | USDMT | Indonesia</Typography>
                            <Chart options={options} series={series} type="line" width={'100%'} height={420} />
                            <Typography sx={{ color: "#002266", fontSize: "10px", textAlign: "center", paddingBottom: "10px", fontWeight: "bold" }}>-Crude Palm Oil(Indonesia, Spot FD, USD/MT)</Typography>
                        </Box>
                    </Grid>
                    <Grid item xs='12' lg='3.9' mr={2}>
                        <Box borderRadius={'8px'} bgcolor={'#f9f8ff'} width={'100%'} height={'100%'} border={'1px solid #ccc'}>
                            <Grid container item justifyContent={'center'} alignItems={'center'} minHeight={'100%'}>
                                <Grid item xs='12' p={'50px'}>
                                    <Typography textAlign={'center'} fontSize={'26px'} fontWeight={'bolder'} color={'#002265'} p={'10px 0px'}>{`${totalPrice} ${'.00'}`}</Typography>
                                    <Typography textAlign={'center'} color={'#b1afaf'} fontSize={'12px'} fontWeight={'bolder'}>USD/MT</Typography>
                                    <Typography textAlign={'center'} color={'#b1afaf'} fontSize={'14px'} fontWeight={'bolder'} pb={'20px'} borderBottom={'1px solid #ccc'}>Indonesia</Typography>
                                    <Typography textAlign={'center'} color={'#b1afaf'} fontSize={'12px'} fontWeight={'bolder'} pt={2}>%M-O-M Change</Typography>
                                    <Box display={'flex'} justifyContent={'space-around'} alignItems={'center'} p={'20px 0px'} borderBottom={'1px solid #ccc'}>
                                        <Typography fontSize={'24px'} color={'#002265'}>N/A</Typography>
                                        <Typography color={'#002265'} pb={'20px'}>N/A</Typography>
                                    </Box>
                                    <Typography textAlign={'center'} color={'#b1afaf'} fontSize={'12px'} fontWeight={'bolder'} pt={2}>%M-O-M Change</Typography>
                                    <Box display={'flex'} justifyContent={'space-around'} alignItems={'center'} p={'20px 0px'} borderBottom={'1px solid #ccc'}>
                                        <Typography fontSize={'24px'} color={'#002265'}>13.61 %</Typography>
                                        <Typography color={'#002265'} pb={'20px'}>1470.00</Typography>
                                    </Box>
                                    <Typography textAlign={'center'} color={'#b1afaf'} fontSize={'12px'} fontWeight={'bolder'} pt={2}>%M-O-M Change</Typography>
                                    <Box display={'flex'} justifyContent={'space-around'} alignItems={'center'} p={'20px 0px'} borderBottom={'1px solid #ccc'}>
                                        <Typography fontSize={'24px'} color={'#002265'}>27.48%</Typography>
                                        <Typography color={'#002265'} pb={'20px'}>1310.00</Typography>
                                    </Box>
                                </Grid>
                            </Grid>
                        </Box>
                    </Grid>
                    <Grid item xs='12'>
                        <Grid container item justifyContent={'center'}>
                            <Grid item xs='6'>
                                <Button variant='contained' fullWidth sx={{ backgroundColor: "#002265" }} onClick={backClickHandler}>Back</Button>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </Grid>
    </Box>
}
