export const getErrorMsz = (type, value, value2) => {

    if (type == "email") {
        const validEmail = new RegExp('^[a-zA-Z0-9._:$!%-+]+@[a-zA-Z0-9.-]+\\.{1}[a-zA-Z]+$');
        return value == "" ? "The value is required" : (!validEmail.test(value) || (value.length > 40)) ? "Invalid email address" : ""
    }

    if (type == "password") {
        return value == "" ? "The value is required" : ""
    }

    if (type == "price") {
        return value == "" ? "The value is required" : ""
    }

    if (type == "date") {
        return value == "" ? "The value is required" : ""
    }

}


export const afterValidate = (callBack) => {
    var errorMszDom = [];
    setTimeout(() => {
        errorMszDom = document.getElementsByClassName("errorDom");
        if (errorMszDom.length == 0) {
            callBack()
        }
    });
}