import { Box, Button, Grid, Typography } from '@mui/material'
import React, { useState } from 'react'
import { CustomTextField } from '../Components/CustomTextField'
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import LockIcon from '@mui/icons-material/Lock';
import { afterValidate, getErrorMsz } from '../Components/Validator';
import { useNavigate } from 'react-router-dom';



export const Login = () => {
    const navigate = useNavigate();

    const [pagedata, setPagedata] = useState({
        email: "",
        password: ""
    })
    const [submitFlag, setSubmitFlag] = useState(false)


    const afterValidateCallBack = () => {
        setTimeout(() => {
            navigate('/dashboard')
        }, 3000)
    }

    const loginClickHandler = () => {
        afterValidate(afterValidateCallBack)
        setSubmitFlag(true)
    }


    return <Box>
        <Grid container justifyContent={'center'} alignItems={'center'} minHeight={'100vh'}>
            <Grid item xs='12' sm='8' md='6' lg='4' p={'50px 20px'} bgcolor={'#f0f0f06b'} border={'1px solid #ccc'}>
                <Grid container item spacing={3}>
                    <Grid item xs='12'>
                        <Typography variant='h4' pb={'20px'} fontWeight={'bolder'}>LOGIN</Typography>
                        <CustomTextField
                            type='text'
                            inputLabel='Email Address'
                            startIcon={<MailOutlineIcon />}
                            value={pagedata?.email}
                            onChange={(e) => {
                                setPagedata({ ...pagedata, email: e.target.value })
                            }}
                            required
                            error={submitFlag && getErrorMsz('email', pagedata.email) != ""}
                            errorMsz={getErrorMsz('email', pagedata.email)}
                        />
                    </Grid>
                    <Grid item xs='12'>
                        <CustomTextField
                            type='password'
                            startIcon={<LockIcon />}
                            inputLabel='Password'
                            value={pagedata?.password}
                            onChange={(e) => {
                                setPagedata({ ...pagedata, password: e.target.value })
                            }}
                            required
                            error={submitFlag && getErrorMsz('password', pagedata.password) != ""}
                            errorMsz={getErrorMsz('password', pagedata.password)}
                        />
                    </Grid>
                    <Grid item xs='12'>
                        <Typography fontWeight={'500'} sx={{ cursor: 'pointer' }} >Forgot Password</Typography>
                    </Grid>
                    <Grid item xs='12'>
                        <Button onClick={loginClickHandler} variant='contained' fullWidth sx={{ padding: "10px 0px" }}>
                            Log in
                        </Button>
                    </Grid>
                </Grid>
            </Grid>
        </Grid>
    </Box>
}
